
.buttonsDiv {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 1rem;
    padding-bottom: 1rem;
}
button {
    cursor: pointer;
    background-color: var(--zero-color);
    color: white;
    border: 1px solid grey;
    padding-left: 8px;
    padding-right: 8px;
    padding-top: 8px;
    padding-bottom: 8px;
}
button:hover {
    background-color: white;
    color: black;
}
button:focus {
    border: 1px solid var(--plus-three-color);   
    outline: none;
}
input {
    background-color: transparent;
    border: 1px solid grey;
    color: white;
    padding-left: 8px;
    padding-right: 8px;
    padding-top: 3px;
    padding-bottom: 3px;
}
input::-webkit-input-placeholder {
    font-family: 'Exo 2', sans-serif;
    color: grey;
}
input:-ms-input-placeholder {
    font-family: 'Exo 2', sans-serif;
    color: grey;
}
input::-ms-input-placeholder {
    font-family: 'Exo 2', sans-serif;
    color: grey;
}
input::placeholder {
    font-family: 'Exo 2', sans-serif;
    color: grey;
}
input:focus {
    border: 1px solid var(--plus-three-color);
    outline: none;
}
input:focus::-webkit-input-placeholder {
    color: grey;
}
input:focus:-ms-input-placeholder {
    color: grey;
}
input:focus::-ms-input-placeholder {
    color: grey;
}
input:focus::placeholder {
    color: grey;
}


